<template>
  <div class="app-modal" @click="clickWrap">
    <div class="app-modal__container">
      <button class="app-modal__close" @click.prevent="closeModal">
        <i class="fa fa-close"></i>
      </button>

      <p>
        You don't have permission to view this forum page as you are an
        <span>unregistered member</span> with limited access.
        <br />
        <button class="app-modal__link pd__register" @click="$emit('signUp')">Register</button> or
        <button class="app-modal__link pd__login" @click="$emit('login')">log in</button> to your account to view
        this section.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    window.addEventListener("keyup", this.keyEvent);
  },
  beforeDestroy() {
    window.removeEventListener("keyup", this.keyEvent);
  },
  methods: {
    clickWrap(e) {
      if (e.target.classList.value === "app-modal") return this.closeModal();
    },
    keyEvent(e) {
      if (e.key === "Escape") this.closeModal();
    },
    closeModal() {
      this.$emit("close");
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/style/variables.scss";

.pd {
  &__register {
    padding-left: 0;
  }

  &__register,
  &__login {
    color: $theme-active !important;
  }
}
</style>