<template>
  <transition name="fade">
    <div id="app" class="app">
      <div class="app-modal app-modal__loading" v-if="getAccess && loading">
        <clip-loader :loading="loading" color="#4cea5e" />
      </div>

      <permission-denied
        v-if="isPermissionDenied"
        @close="closePermissionForm"
        @signUp="openSignUpForm"
        @login="openLoginForm"
      />

      <login-form
        v-if="isLoginForm"
        :activeUsername="activeUsername"
        :activePassword="activePassword"
        @close="closeLoginForm"
        @signUp="signUpFromLogin"
      />

      <sign-up-form v-if="isSignUpForm" @close="closeSignUpForm" />

      <app-header
        @login="openLoginForm"
        @signUp="openSignUpForm"
        @permission-denied="openPermissionForm"
      />

      <app-breadcrumb
        v-if="getAccess"
        @permission-denied="openPermissionForm"
      />

      <div class="section main">
        <div class="app-wrap" v-if="getAccess && firstRenderer">
          <app-pagination
            :page="activePage"
            @change="changePage"
            @permission-denied="openPermissionForm"
          />

          <h1 class="heading">
            MARX Middleman (Escrow) Service | Always Available | Fast & Easy |
            Instant Replies
          </h1>

          <component :is="`page-${activePage}`" />

          <app-pagination
            :page="activePage"
            position="bottom"
            @change="changePage"
            @permission-denied="openPermissionForm"
          />
        </div>

        <!-- <div v-else-if="!getAccess">
          <div class="main__no-access">
            <p class="main__no-access-404">404</p>
            <span class="color-red"> Page not found </span> <br />
          </div>
        </div> -->

        <div v-else class="main__empty">Waiting for connection ...</div>
      </div>

      <app-footer @permission-denied="openPermissionForm" />
    </div>
  </transition>
</template>

<script>
import AppHeader from "@/components/Header.vue";
import AppFooter from "@/components/Footer.vue";
import AppBreadcrumb from "@/components/Breadcrumb.vue";
import AppPagination from "@/components/Pagination.vue";
import LoginForm from "@/components/LoginForm.vue";
import SignUpForm from "@/components/SignUpForm.vue";
import PermissionDenied from "@/components/PermissionDenied.vue";
import { bus } from "@/main";
import ClipLoader from "vue-spinner/src/ClipLoader.vue";

//pages
import Page1 from "@/components/Pages/Page1.vue";
import Page2 from "@/components/Pages/Page2.vue";
import Page3 from "@/components/Pages/Page3.vue";
import Page4 from "@/components/Pages/Page4.vue";
import Page5 from "@/components/Pages/Page5.vue";

export default {
  name: "Marx",
  components: {
    AppHeader,
    AppFooter,
    AppBreadcrumb,
    AppPagination,
    LoginForm,
    SignUpForm,
    ClipLoader,
    PermissionDenied,
    Page1,
    Page2,
    Page3,
    Page4,
    Page5,
  },
  data() {
    return {
      activePage: null,
      loading: true,
      isPermissionDenied: false,
      firstRenderer: false,
      isLoginForm: false,
      isSignUpForm: false,
      activeUsername: "",
      activePassword: "",
      getAccess: false,
      homePage: false,
    };
  },
  created() {
    bus.$on("permission-denied", this.openPermissionForm);
  },
  mounted() {
    this.getAccess = true;
    this.changePage(1);
  },
  beforeDestroy() {
    bus.$off("permission-denied", this.openPermissionForm);
  },
  methods: {
    openLoginForm() {
      document.body.classList.add("o-hidden");
      this.isLoginForm = true;
    },
    closeLoginForm(username, password) {
      this.saveLoginFormData(username, password);
      document.body.classList.remove("o-hidden");
    },
    saveLoginFormData(username, password) {
      this.activeUsername = username;
      this.activePassword = password;
      this.isLoginForm = false;
    },
    signUpFromLogin(username, password) {
      this.saveLoginFormData(username, password);
      this.isSignUpForm = true;
    },
    openPermissionForm() {
      this.isPermissionDenied = true;
      document.body.classList.add("o-hidden");
    },
    closePermissionForm() {
      this.isPermissionDenied = false;
      document.body.classList.remove("o-hidden");
    },
    openSignUpForm() {
      this.isSignUpForm = true;
      this.isPermissionDenied = false;
      document.body.classList.add("o-hidden");
    },
    openLoginForm() {
      this.isLoginForm = true;
      this.isPermissionDenied = false;
      document.body.classList.add("o-hidden");
    },
    closeSignUpForm() {
      this.isSignUpForm = false;
      document.body.classList.remove("o-hidden");
    },
    changePage(val, position) {
      if (val === this.activePage) return;
      this.loading = true;
      document.body.classList.add("o-hidden");
      if (position === "bottom") this.nextLink("app");

      setTimeout(() => {
        this.activePage = val;
        this.loading = false;
        document.body.classList.remove("o-hidden");
        if (!this.firstRenderer) this.firstRenderer = true;
        // if (position === 'bottom') this.nextLink('app')
      }, 750); // 750
    },
    nextLink(name) {
      // if (name.includes(" ")) name = name.replace(" ", "-");
      document
        .querySelector(`.${name}`)
        .scrollIntoView({ block: "start", behavior: "smooth" });
    },
  },
};
</script>