<template>
  <div class="app-modal" @click="clickWrap">
    <div class="app-modal__container sign-up">
      <button class="app-modal__close" @click.prevent="closeModal">
        <i class="fa fa-close"></i>
      </button>

      <h3 class="sign-up__heading">Registration</h3>

      <div class="sign-up__container">
        <p><b>Welcome to FLP.WTF Forum!</b></p>
        <p>Our forum is a <b>CLOSED</b> board.</p>
        <p>
          You can get access the forum in two ways: with free registration if
          you have a vouches on other forums or with paid registration.
        </p>
        <p>
          <b>Paid registration:</b> <br />
          • Cost of paid registration on the forum is $200. Registration is
          processed manually. <br />
          • Your account would not be activated until you will made the payment.
          At this stage, it will have the “Not activated” group. <br />
          • After payment and one confirmations in blockchain network, account
          will receive "Paid registration" group. Members with "Paid
          registration" have the same privileges as members with "User" group.
        </p>
        <p>
          To receive address for funding or provide your vouches on other
          forums, please contact support on Jabber
          <a href="mailto:support@exploit.im" class="theme-active">support@flp.wtf</a>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    window.addEventListener("keyup", this.keyEvent);
  },
  beforeDestroy() {
    window.removeEventListener("keyup", this.keyEvent);
  },
  methods: {
    clickWrap(e) {
      if (e.target.classList.value === "app-modal") return this.closeModal();
    },
    keyEvent(e) {
      if (e.key === "Escape") this.closeModal();
    },
    closeModal() {
      this.$emit("close");
    },
  },
};
</script>

