import Vue from 'vue';
import VueRouter from 'vue-router';
// import App from "@/App"
import Home from "@/pages/Home.vue"
import Marx from "@/pages/Marx.vue"
import PageNotFound from "@/pages/404.vue"

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "Home",
		component: Home
	},
	{
		path: "/marx-escrow",
		name: "Marx",
		component: Marx
	},
	{
		path: "*",
		component: PageNotFound
	}
];

const router = new VueRouter({
	mode: 'history',
	// base: '/test2/',
	routes,
});

export default router;