<template>
  <div class="app-modal" @click="clickWrap">
    <form class="app-modal__container login" @submit.prevent="submitForm">
      <clip-loader class="login__loading" :loading="loading" color="#4cea5e" />

      <button class="app-modal__close" @click.prevent="closeModal">
        <i class="fa fa-close"></i>
      </button>

      <div class="login__row">
        <label for="username"> Username </label>
        <input
          type="text"
          :disabled="loading"
          v-model="username"
          name="username"
          ref="1"
          :class="{ 'login__row--error': isUsernameError }"
          @keydown="usernameInput"
          @keydown.enter.prevent="submitForm"
        />
      </div>

      <div class="login__row">
        <label for="password"> Password </label>
        <input
          type="text"
          :disabled="loading"
          v-model="password"
          name="password"
          ref="2"
          :class="{ 'login__row--error': isPasswordError }"
          @keydown="passwordInput"
          @keydown.enter.prevent="submitForm"
        />

        <p v-if="error" class="login__error">Wrong username or password!</p>
      </div>

      <button
        class="app-button app-button--red"
        type="submit"
        :disabled="loading"
      >
        Login
      </button>

      <div class="login__bottom">
        <span> or </span>

        <button class="app-modal__link" @click.prevent="signUp">Sign Up</button>
      </div>
    </form>
  </div>
</template>

<script>
import ClipLoader from "vue-spinner/src/ClipLoader.vue";

export default {
  components: {
    ClipLoader,
  },
  props: {
    activeUsername: String,
    activePassword: String,
  },
  data() {
    return {
      username: "",
      password: "",
      loading: false,
      error: false,
      isUsernameError: false,
      isPasswordError: false,
    };
  },
  created() {
    this.username = this.activeUsername;
    this.password = this.activePassword;

    window.addEventListener("keyup", this.keyEvent);
  },
  mounted() {
    this.initialFocus();
  },
  beforeDestroy() {
    window.removeEventListener("keyup", this.keyEvent);
  },
  methods: {
    clickWrap(e) {
      if (e.target.classList.value === "app-modal") return this.closeModal();
    },
    initialFocus() {
      if (this.username.length && !this.password.length)
        return this.$refs[2].focus();
      this.$refs[1].focus();
    },
    usernameInput(e) {
      this.error = false;
      if (e.key === "Enter") {
        if (!this.password.length && this.username.length) {
          this.$refs[2].focus();
        }
        return;
      }
      if (e.key === "Backspace") {
        // if (e.target.value.length === 1) this.isUsernameError = true;
        return;
      }
      this.isUsernameError = false;
    },
    passwordInput(e) {
      this.error = false;
      if (e.key === "Enter" || e.key === "Backspace") return;
      this.isPasswordError = false;
    },
    submitForm() {
      if (!this.username.length) {
        this.isUsernameError = true;
        if (!this.password.length) this.isPasswordError = true;
        return;
      } else if (!this.password.length) return (this.isPasswordError = true);

      this.loading = true;
      setTimeout(() => {
        this.loading = false;
        this.error = true;

        setTimeout(() => {
          this.$refs[1].focus();
        }, 250);
      }, 2000);
    },
    keyEvent(e) {
      if (e.key === "Escape") this.closeModal();
    },
    signUp() {
      this.$emit("signUp", this.username, this.password);
    },
    closeModal() {
      this.$emit("close", this.username, this.password);
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/style/variables.scss";

.login {
  &__row {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    padding-bottom: 20px;

    label {
      font-size: 14px;
    }

    input {
      border: 1px solid #464646;
      background-color: #242424;
      color: white;
      border-radius: 5px;
      padding: 15px;
      height: 40px;
      outline: none;
      font-size: 15px;

      &:disabled {
        opacity: 0.4;
      }
    }

    &--error {
      border: 1px solid $color-danger !important;
    }
  }

  &__error {
    position: absolute;
    bottom: -4px;
    width: 100%;
    text-align: center;
    color: $color-danger;
  }

  &__loading {
    position: absolute;

    .v-clip {
      width: 64px !important;
      height: 64px !important;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 16px;
  }

  .app-button {
    margin-top: 10px;
  }
}
</style>